import { Grid, Typography } from '@mui/material'
import React from 'react'
import { useSelector } from 'react-redux';

export const ProductOperation = () => {
    const lang = useSelector((state)=>state.value);
    const section ={
        head1:{eng:'Compact for one man operability',kan:'ಒನ್ ಮ್ಯಾನ್ ಆಪರೇಬಿಲಿಟಿಗಾಗಿ ಕಾಂಪ್ಯಾಕ್ಟ್'},
        sub1:{eng:'The H12 series uses newest Qualcomm Snapdragon 625 processor, equipped with an Android embedded system and advanced SDR technology and super protocol stack, making the image clearer, lower the latency, longer distance.',
        kan:'H12 ಸರಣಿಯು ಹೊಸದಾದ Qualcomm Snapdragon 625 ಪ್ರೊಸೆಸರ್ ಅನ್ನು ಬಳಸುತ್ತದೆ, ಇದು Android ಎಂಬೆಡೆಡ್ ಸಿಸ್ಟಮ್ ಮತ್ತು ಸುಧಾರಿತ SDR ತಂತ್ರಜ್ಞಾನ ಮತ್ತು ಸೂಪರ್ ಪ್ರೋಟೋಕಾಲ್ ಸ್ಟಾಕ್ ಅನ್ನು ಹೊಂದಿದೆ, ಇದು ಚಿತ್ರವನ್ನು ಸ್ಪಷ್ಟಗೊಳಿಸುತ್ತದೆ, ಸುಪ್ತತೆಯನ್ನು ಕಡಿಮೆ ಮಾಡುತ್ತದೆ, ಹೆಚ್ಚು ದೂರವನ್ನು ಮಾಡುತ್ತದೆ.'},
        head2:{eng:'Intuitive and easy to use',kan:'ಅರ್ಥಗರ್ಭಿತ ಮತ್ತು ಬಳಸಲು ಸುಲಭ'},
        sub2:{eng:'With the help of software, user can directly mark coordinates point to the field to which the chemical needs to be sprayed and the drone can automatically take off, once task is complete the drone return to the default coordinates/start coordinates',
        kan:'ಸಾಫ್ಟ್‌ವೇರ್ ಸಹಾಯದಿಂದ, ಬಳಕೆದಾರರು ರಾಸಾಯನಿಕವನ್ನು ಸಿಂಪಡಿಸಬೇಕಾದ ಕ್ಷೇತ್ರಕ್ಕೆ ನಿರ್ದೇಶಾಂಕಗಳನ್ನು ನೇರವಾಗಿ ಗುರುತಿಸಬಹುದು ಮತ್ತು ಡ್ರೋನ್ ಸ್ವಯಂಚಾಲಿತವಾಗಿ ಟೇಕ್ ಆಫ್ ಆಗಬಹುದು, ಒಮ್ಮೆ ಕಾರ್ಯ ಪೂರ್ಣಗೊಂಡ ನಂತರ ಡ್ರೋನ್ ಡೀಫಾಲ್ಟ್ ನಿರ್ದೇಶಾಂಕಗಳಿಗೆ/ಪ್ರಾರಂಭದ ನಿರ್ದೇಶಾಂಕಗಳಿಗೆ ಹಿಂತಿರುಗುತ್ತದೆ.'},
        head3:{eng:'24.1 MP Imaging Payload',kan:'24.1 MP ಇಮೇಜಿಂಗ್ ಪೇಲೋಡ್'},
        sub3:{eng:'11L Quadra carries a 24.1 MP imaging camera with an APS-C sensor for capturing sharp, high-resolution images. The camera is mounted nadir-facing on a custom designed mount to reduce the effect of vibrations on the images.',
        kan:'11L ಕ್ವಾಡ್ರಾ 24.1 MP ಇಮೇಜಿಂಗ್ ಕ್ಯಾಮೆರಾವನ್ನು APS-C ಸಂವೇದಕದೊಂದಿಗೆ ಚೂಪಾದ, ಹೆಚ್ಚಿನ ರೆಸಲ್ಯೂಶನ್ ಚಿತ್ರಗಳನ್ನು ಸೆರೆಹಿಡಿಯಲು ಹೊಂದಿದೆ. ಚಿತ್ರಗಳ ಮೇಲಿನ ಕಂಪನಗಳ ಪರಿಣಾಮವನ್ನು ಕಡಿಮೆ ಮಾಡಲು ಕಸ್ಟಮ್ ವಿನ್ಯಾಸದ ಮೌಂಟ್‌ನಲ್ಲಿ ಕ್ಯಾಮೆರಾವನ್ನು ನಾದಿರ್-ಫೇಸಿಂಗ್ ಮಾಡಲಾಗಿದೆ.'}
    }
    return (
        <div>
            <div className='bg-secondary bg-opacity-25 product-bg-img1'>
                <Grid container display={'flex'} justifyContent={'space-between'} alignItems={'center'} pb={2}>
                    <Grid item xs={12} md={5} display={'flex'} justifyContent={'start'}>
                        <img src="assets/drone-console.webp" alt="" style={{ maxWidth: '100%' }} />
                    </Grid>
                    <Grid container item xs={12} md={7} display={'flex'} justifyContent={'center'}>
                        <Grid item xs={11} md={9}>
                            <Typography className='ff-mi' variant='h4' marginY={2}>{section.head1[lang]}</Typography>
                            {/* <div className='d-flex justify-content-start' style={{ marginTop: -100 }}><img src="assets/element-blur-2 1.web" alt="" height={150} /></div> */}
                            <Typography color={'#656565'}>{section.sub1[lang]}</Typography>
                        </Grid>

                    </Grid>

                </Grid>
            </div>
            <div className='av-bg-primary product-bg-img2'>
                <Grid container display={'flex'} justifyContent={'space-between'} alignItems={'center'}>
                    <Grid container item xs={12} md={6} display={'flex'} justifyContent={'center'}>
                        <Grid item xs={11} md={10}>
                            <Typography className='ff-mi' variant='h4' color={'white'} marginY={2}>{section.head2[lang]}</Typography>
                            {/* <div className='d-flex justify-content-start' style={{ marginTop: -100 }}><img src="assets/element-blur-2 1.web" alt="" height={150} /></div> */}
                            <Typography color={'#DCDCDC'}>{section.sub2[lang]}</Typography>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} md={5} >
                        <img src="assets/drone-view.webp" width={'100%'} alt="" />
                    </Grid>
                </Grid>
            </div>
            <div className='bg-secondary bg-opacity-25 product-bg-img3'>
                <Grid container display={'flex'} justifyContent={'space-between'} alignItems={'center'}>
                    <Grid item xs={12} md={7} display={'flex'} justifyContent={'start'}>
                        <img src="assets/drone-camera.webp" width={'100%'} alt="" />
                    </Grid>
                    <Grid container item xs={12} md={5} display={'flex'} justifyContent={'center'} py={2}>
                        <Grid item xs={11} md={9} >
                            <Typography className='ff-mi' variant='h4' marginY={2}>{section.head3[lang]}</Typography>
                            {/* <div className='d-flex justify-content-start' style={{ marginTop: -100 }}><img src="assets/element-blur-2 1.web" alt="" height={150} /></div> */}
                            <Typography color={'#656565'}>{section.sub3[lang]}</Typography>
                        </Grid>

                    </Grid>

                </Grid>
            </div>
        </div>
    )
}
