import { Grid, Typography } from '@mui/material'
import React from 'react'
import { useSelector } from 'react-redux';

export const ProductPerformance = () => {
    const lang = useSelector((state)=>state.value);
    const section = {
        head1:{eng:'Performance you can rely on',kan:'ನೀವು ಅವಲಂಬಿಸಬಹುದಾದ ಕಾರ್ಯಕ್ಷಮತೆ'},
        sub1:{eng:'The X8 Hobby Wing Motors: High-performance, reliable, and efficient motors for your drone. Upgrade and enhance your flying experience with excellent thrust and stability. Perfect for aerial photography, racing, and acrobatics. Elevate your drone to new heights with these powerful motors.',
        kan:'X8 ಹವ್ಯಾಸ ವಿಂಗ್ ಮೋಟಾರ್ಸ್: ನಿಮ್ಮ ಡ್ರೋನ್‌ಗಾಗಿ ಹೆಚ್ಚಿನ ಕಾರ್ಯಕ್ಷಮತೆ, ವಿಶ್ವಾಸಾರ್ಹ ಮತ್ತು ಪರಿಣಾಮಕಾರಿ ಮೋಟಾರ್‌ಗಳು. ಅತ್ಯುತ್ತಮ ಒತ್ತಡ ಮತ್ತು ಸ್ಥಿರತೆಯೊಂದಿಗೆ ನಿಮ್ಮ ಹಾರುವ ಅನುಭವವನ್ನು ಅಪ್‌ಗ್ರೇಡ್ ಮಾಡಿ ಮತ್ತು ವರ್ಧಿಸಿ. ವೈಮಾನಿಕ ಛಾಯಾಗ್ರಹಣ, ರೇಸಿಂಗ್ ಮತ್ತು ಚಮತ್ಕಾರಿಕಗಳಿಗೆ ಪರಿಪೂರ್ಣ. ಈ ಶಕ್ತಿಯುತ ಮೋಟಾರ್‌ಗಳೊಂದಿಗೆ ನಿಮ್ಮ ಡ್ರೋನ್ ಅನ್ನು ಹೊಸ ಎತ್ತರಕ್ಕೆ ಏರಿಸಿ.'},
        head2:{eng:'High-performance battery',kan:'ಹೆಚ್ಚಿನ ಕಾರ್ಯಕ್ಷಮತೆಯ ಬ್ಯಾಟರಿ'},
        sub2:{eng:'Tattu 16000mAh 22.2V 15/30C 6S1P Lipo Battery Pack with huge capacity for long endurance. It provides stable power with no swelling or overheating at all.',
        kan:'Tattu 16000mAh 22.2V 15/30C 6S1P Lipo ಬ್ಯಾಟರಿ ಪ್ಯಾಕ್ ದೀರ್ಘ ಸಹಿಷ್ಣುತೆಗಾಗಿ ದೊಡ್ಡ ಸಾಮರ್ಥ್ಯದೊಂದಿಗೆ. ಇದು ಯಾವುದೇ ಊತ ಅಥವಾ ಅಧಿಕ ಬಿಸಿಯಾಗದಂತೆ ಸ್ಥಿರವಾದ ಶಕ್ತಿಯನ್ನು ಒದಗಿಸುತ್ತದೆ.'},
        cap:{eng:'Capacity',kan:'ಸಾಮರ್ಥ್ಯ'},
        vol:{eng:'Voltage',kan:'ವೋಲ್ಟೇಜ್'},
        dis:{eng:'Discharge Rate',kan:'ವಿಸರ್ಜನೆ ದರ'},
        wet:{eng:'Weight',kan:'ತೂಕ'},
        dim:{eng:'Dimensions',kan:'ಆಯಾಮಗಳು'},
        head3:{eng:'Survey-grade accuracy',kan:'ಸಮೀಕ್ಷೆ-ದರ್ಜೆಯ ನಿಖರತೆ'},
        sub3:{eng:'Achieve survey-grade accuracy with our advanced drone technology. Our drones are equipped with high-precision sensors and GPS capabilities, ensuring precise and reliable data collection for mapping, surveying, and land analysis. Experience the highest level of accuracy and efficiency in your projects with our survey-grade drones.',
        kan:'ನಮ್ಮ ಸುಧಾರಿತ ಡ್ರೋನ್ ತಂತ್ರಜ್ಞಾನದೊಂದಿಗೆ ಸಮೀಕ್ಷೆ-ದರ್ಜೆಯ ನಿಖರತೆಯನ್ನು ಸಾಧಿಸಿ. ನಮ್ಮ ಡ್ರೋನ್‌ಗಳು ಹೆಚ್ಚಿನ ನಿಖರವಾದ ಸಂವೇದಕಗಳು ಮತ್ತು GPS ಸಾಮರ್ಥ್ಯಗಳೊಂದಿಗೆ ಸಜ್ಜುಗೊಂಡಿವೆ, ಮ್ಯಾಪಿಂಗ್, ಸಮೀಕ್ಷೆ ಮತ್ತು ಭೂ ವಿಶ್ಲೇಷಣೆಗಾಗಿ ನಿಖರ ಮತ್ತು ವಿಶ್ವಾಸಾರ್ಹ ಡೇಟಾ ಸಂಗ್ರಹಣೆಯನ್ನು ಖಚಿತಪಡಿಸುತ್ತದೆ. ನಮ್ಮ ಸಮೀಕ್ಷೆ-ದರ್ಜೆಯ ಡ್ರೋನ್‌ಗಳೊಂದಿಗೆ ನಿಮ್ಮ ಪ್ರಾಜೆಕ್ಟ್‌ಗಳಲ್ಲಿ ಅತ್ಯುನ್ನತ ಮಟ್ಟದ ನಿಖರತೆ ಮತ್ತು ದಕ್ಷತೆಯನ್ನು ಅನುಭವಿಸಿ.'},
    }
    return (
        <div>
            <Grid container display={'flex'} justifyContent={'end'} alignItems={'center'}>
                <Grid container item xs={12} md={5} display={'flex'} justifyContent={'center'}>
                    <Grid item xs={11} md={10} >
                        <Typography className='ff-mi' variant='h4' marginY={2}>{section.head1[lang]}</Typography>
                        {/* <div className='d-flex justify-content-start' style={{ marginTop: -100 }}><img src="assets/element-blur-2 1.webp" alt="" height={150} /></div> */}
                        <Typography color={'#656565'}>{section.sub1[lang]}</Typography>
                    </Grid>
                </Grid>
                <Grid item xs={12} md={6} display={'flex'} justifyContent={'end'}>
                    <img src="assets/drone-left.webp" width={'100%'} alt="" />
                </Grid>
            </Grid>

            <div className='bg-secondary bg-opacity-25 product-bg-img1'>
                <Grid container display={'flex'} justifyContent={'start'} alignItems={'center'}>
                    <Grid item xs={12} md={6} display={'flex'} justifyContent={'start'}>
                        <img src="assets/drone-right.webp" width={'100%'} alt="" />
                    </Grid>
                    <Grid container item xs={12} md={5} display={'flex'} justifyContent={'center'} py={3}>
                        <Grid item xs={11} md={10} >
                            <Typography className='ff-mi' variant='h4' marginY={2}>{section.head2[lang]}</Typography>
                            {/* <div className='d-flex justify-content-start' style={{ marginTop: -100 }}><img src="assets/element-blur-2 1.webp" alt="" height={150} /></div> */}
                            <Typography color={'#656565'}>{section.sub2[lang]}</Typography>
                            <Typography color={'#656565'} ml={2} my={2}>{section.cap[lang]}: 16000mAh </Typography>
                            <Typography color={'#656565'} ml={2} my={2}>{section.vol[lang]}: 22.2V </Typography>
                            <Typography color={'#656565'} ml={2} my={2}>{section.dis[lang]}: 15Cv </Typography>
                            <Typography color={'#656565'} ml={2} my={2}>{section.wet[lang]}: 1990g </Typography>
                            <Typography color={'#656565'} ml={2} my={2}>{section.dim[lang]}: 193*77*66 mm</Typography>
                        </Grid>

                    </Grid>

                </Grid>
            </div>
            <div className='av-bg-primary'>
                <Grid container display={'flex'} justifyContent={'space-between'} alignItems={'center'}>
                    <Grid container item xs={12} md={6} display={'flex'} justifyContent={'center'} py={2}>
                        <Grid item xs={11} md={10}>
                            <Typography className='ff-mi' variant='h4' color={'white'} marginY={2}>{section.head3[lang]}</Typography>
                            {/* <div className='d-flex justify-content-start' style={{ marginTop: -100 }}><img src="assets/element-blur-2 1.webp" alt="" height={150} /></div> */}
                            <Typography color={'#DCDCDC'}>{section.sub3[lang]}</Typography>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} md={5} >
                        <img src="assets/drone-fold.webp" width={'100%'} alt="" />
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}
