        {
            qn: {eng:'Do we need insurance? Is it available from the company ?',kan:'ನಮಗೆ ವಿಮೆ ಬೇಕೇ? ಇದು ಕಂಪನಿಯಿಂದ ಲಭ್ಯವಿದೆಯೇ?'},
            ans: {eng:'Insurance Details will be available form November 2023 ',kan:'ವಿಮಾ ವಿವರಗಳು ನವೆಂಬರ್ 2023 ರಲ್ಲಿ ಲಭ್ಯವಿರುತ್ತವೆ'}
        }
