            ans: {eng:'Sindhanur, Maski, Manvi, Raichur, Sirguppa, Gangavati, Hampi, Hospet',kan:'ಸಿಂಧನೂರು, ಮಸ್ಕಿ, ಮಾನ್ವಿ, ರಾಯಚೂರು, ಸಿರಗುಪ್ಪ, ಗಂಗಾವತಿ, ಹಂಪಿ, ಹೊಸಪೇಟೆ'}
