const Accordion = styled((props) => (
    <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
    border: `1px solid ${theme.palette.divider}`,
    '&:not(:last-child)': {
        borderBottom: 0,
    },
    '&:before': {
        display: 'none',
    },
}));

const AccordionSummary = styled((props) => (
    <MuiAccordionSummary
        expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />}
        {...props}
    />
))(({ theme }) => ({
    backgroundColor:
        theme.palette.mode === 'dark'
            ? 'rgba(255, 255, 255, .05)'
            : '#ffffff',
    flexDirection: 'row-reverse',
    '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
        transform: 'rotate(90deg)',
    },
    '& .MuiAccordionSummary-content': {
        marginLeft: theme.spacing(1),
    },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
    padding: theme.spacing(2),
    borderTop: '1px solid rgba(0, 0, 0, .125)',
}));

export const ContactUsAccordian = () => {
    const [expanded, setExpanded] = React.useState('panel1');
    const lang = useSelector((state) => state.value);
    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };
    const headers = {
        gen:{eng:'General questions',kan:'ಸಾಮಾನ್ಯ ಪ್ರಶ್ನೆಗಳು'},
        bat:{eng:'Batteries',kan:'ಬ್ಯಾಟರಿಗಳು'},
        sal:{eng:'Sales',kan:'ಮಾರಾಟ'},
        mai:{eng:'Maintenance',kan:'ನಿರ್ವಹಣೆ'},
        ren:{eng:'Rentals',kan:'ಬಾಡಿಗೆಗಳು'},
    }

    return (
        <div className='mt-5'>
            <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
                    <Typography fontWeight={700} className='ff-mi'>{headers.gen[lang]}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ol className="list-group list-group-numbered list-group-flush border-0">
                        {faqs.general.map((el) => (
                            <li key={el.qn['eng']} className="list-group-item d-flex">
                                <div className="ms-2 me-auto">
                                    <div className="fw-bold">{el.qn[lang]}</div>
                                    <div dangerouslySetInnerHTML={{ __html: el.ans[lang] }}></div>
                                </div>
                            </li>
                        ))}
                    </ol>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                <AccordionSummary aria-controls="panel2d-content" id="panel2d-header">
                    <Typography fontWeight={700} classNameName='ff-mi'>{headers.bat[lang]}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ol className="list-group list-group-numbered list-group-flush border-0">
                        {faqs.battery.map((el) => (
                            <li key={el.qn['eng']} className="list-group-item d-flex">
                                <div className="ms-2 me-auto">
                                    <div className="fw-bold">{el.qn[lang]}</div>
                                    <div dangerouslySetInnerHTML={{ __html: el.ans[lang] }}></div>
                                </div>
                            </li>
                        ))}
                    </ol>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
                <AccordionSummary aria-controls="panel3d-content" id="panel3d-header">
                    <Typography fontWeight={700} classNameName='ff-mi'>{headers.sal[lang]}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ol className="list-group list-group-numbered list-group-flush border-0">
                        {faqs.sales.map((el) => (
                            <li key={el.qn['eng']} className="list-group-item d-flex">
                                <div className="ms-2 me-auto">
                                    <div className="fw-bold">{el.qn[lang]}</div>
                                    <div dangerouslySetInnerHTML={{ __html: el.ans[lang] }}></div>
                                </div>
                            </li>
                        ))}
                    </ol>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
                <AccordionSummary aria-controls="panel4d-content" id="panel4d-header">
                    <Typography fontWeight={700} classNameName='ff-mi'>{headers.mai[lang]}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ol className="list-group list-group-numbered list-group-flush border-0">
                        {faqs.maintenance.map((el) => (
                            <li key={el.qn['eng']} className="list-group-item d-flex">
                                <div className="ms-2 me-auto">
                                    <div className="fw-bold">{el.qn[lang]}</div>
                                    <div dangerouslySetInnerHTML={{ __html: el.ans[lang] }}></div>
                                </div>
                            </li>
                        ))}
                    </ol>
                </AccordionDetails>
            </Accordion>
            <Accordion expanded={expanded === 'panel5'} onChange={handleChange('panel5')}>
                <AccordionSummary aria-controls="panel5d-content" id="panel5d-header">
                    <Typography fontWeight={700} classNameName='ff-mi'>{headers.ren[lang]}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <ol className="list-group list-group-numbered list-group-flush border-0">
                        {faqs.rent.map((el) => (
                            <li key={el.qn['eng']} className="list-group-item d-flex">
                                <div className="ms-2 me-auto">
                                    <div className="fw-bold">{el.qn[lang]}</div>
                                    <div dangerouslySetInnerHTML={{ __html: el.ans[lang] }}></div>
                                </div>
                            </li>
                        ))}
                    </ol>
                </AccordionDetails>
            </Accordion>
        </div>
    );
}
